@font-face{
	font-family: shrikhand;
	src: url(../Shrikhand/Shrikhand-Regular.OpenType);
}

@font-face{
	font-family: roboto-regular;
	src: url(../roboto/Roboto-Regular.ttf);
	font-size: 600;
}

@font-face{
	font-family: roboto-medium;
	src: url(../roboto/Roboto-Medium.ttf);
	font-size: 700;
}

$color-primary: #9356dc;
$color-secondary: #ff79da;
$color-tertiary: #99E2D0;

body{
	width: 100%;
	margin: 0px;
	font-family: roboto-regular;
}

.entete{
	display: flex;
	justify-content: center;
	align-items: center;
	&__flecheRetour{
		position: absolute;
		left: 0;
		font-size: 20px;
		margin-left: 20px;
		color: black;
	}
	& img{
		transform: scale(.5);
	}
}

.photoMenu{
	height: 600px;
	object-fit: cover;
	object-position: center 60%;
	position: relative;
	z-index: -1;
	display: flex;
	width:100%;
}

.menuBigScreen{
	display: flex;
}

@keyframes apparition{
	from{
		transform: scale(0);
		transform-origin: top;
	} to{
		transform: scale(1);
		transform-origin: top;
	}
}

@for $i from 1 through 3 {
		.partieMenu--#{$i}{
			animation: apparition;
			animation-delay: 1000ms * $i;
		}
	}

.detailMenu{
	margin-top: -10%;
	border-radius: 20px 20px 0 0;
	background-color: #f6f6f6;
	padding-bottom: 50px;
	&__entete{
		display: flex;
		justify-content: space-around;
		align-items: center;
		font-family: shrikhand;
	}
	&__titrePrincipal{
		margin-left: 20px;
	}
	& i{
		font-size: 20px;
	}
	&__titre{
		margin-left: 20px;
		text-transform: uppercase;
	}
	&--trait{
		border: 3px solid $color-tertiary;
		width: 50px;
		margin-top: 5px;
	}
	&__entrees{
		width: 33%;
	}
	&__plats{
		width: 33%;
	}
	&__desserts{
		width: 33%;
	}
}

.choix{
	background-color: white;
	display: flex;
	justify-content: space-between;
	align-items: flex-end;
	margin-bottom: 20px;
	margin-left: 20px;
	margin-right: 20px;
	border-radius: 20px;
	padding: 10px;
	&__prix{
		margin-bottom: 16px;
	}
}

.boutonCommander{
	text-align: center;
}

button{
	color: white;
	background-color: $color-secondary;
	padding: 10px;
	width: 50%;
	border-radius: 30px;
	border: none;
	background: radial-gradient(ellipse at top left, $color-secondary, $color-primary);
	& a{
		color: white;
		text-decoration: none;
	}
}

.footer{
	display: flex;
	background-color: #353535;
	padding: 40px;
	&__logo{
		filter: invert(1);
		flex: 0.4;
		transform: scale(0.5);
	}
	&__div{
		flex:0.6;
		display: flex;
	}
	&__menuDinteraction{
		display: flex;
		flex: 2;
		margin: auto;
		padding-left: 0px;
	}
	&__menuDinteraction li{
		width: 25%;
		text-align: end;
		list-style-type: none;
		filter: invert(1);
	}
	& a{
		text-decoration: none;
		color: black;
	}
}

@media screen and (max-width: 800px){
	.footer{
		&__menuDinteraction{
			flex-direction: column;
			& li{
				width: inherit;
			}
		}
	}
}

@media screen and (max-width: 599px){

	.menuBigScreen{
		display: inherit;
	}

	.photoMenu{
		height: 200px;
	}
	.detailMenu{
		&__entete{
			justify-content: space-between;
			width: 90%;
			margin: auto;
		}
		&__titrePrincipal{
			margin-left: 0;
		}
		&__entrees{
			margin-bottom: 40px;
			width: inherit;
		}
		&__plats{
			margin-bottom: 40px;
			width: inherit;
		}
		&__desserts{
			margin-bottom: 40px;
			width: inherit;
		}
	}

	.footer{
		padding: 20px;
		flex-direction: column;
		&__logo{
			transform: scale(0.4);
			transform-origin: left;
		}
		&__div{
			flex: inherit;
		}
		&__menuDinteraction{
			flex-direction: column;
			flex : inherit;
			width: 90%;
			margin-left: 0px;
		}
		&__menuDinteraction li{
			width: inherit;
			margin-bottom: 20px;
			text-align: inherit;
		}
	}
}